import Vue from 'vue'
import App from './App.vue'
import router from './router'
// import './plugins/element.js'

// 导入图标文件
import "./assets/fonts/iconfont.css"
// 导入全局样式表
import './assets/css/global.css'
import TreeTable from "vue-table-with-tree-grid"
// 导入富文本组件
import VueQuillEditor from 'vue-quill-editor'
// 导入富文本样式
// import 'quill/dist/quill.core.css'
// import 'quill/dist/quill.snow.css'
// import 'quill/dist/quill.bubble.css'

import axios from "axios"
// import 'default-passive-events'
// 在request拦截器中，展示进度条NProgress.start()
import NProgress from "nprogress"


// 配置请求的根路径
axios.defaults.baseURL = "http://127.0.0.1:8888/api/private/v1/"
//通过axios请求拦截器添加token，保证拥有获取数据的权限
axios.interceptors.request.use(config => {
    // console.log(config);
    // 开始加载
    NProgress.start()
    // 为请求头对象，添加Token验证段Authorization
    config.headers.Authorization = window.sessionStorage.getItem("token")
    return config
})
// 在response拦截器中
axios.interceptors.response.use(config => {
    NProgress.done()
    return config
})

// 将请求路径挂载到原型
Vue.prototype.$http = axios

Vue.config.productionTip = false

// 进行注册为全局可用组件
Vue.component("tree-table", TreeTable)
// 将富文本注册为全局可用组件
Vue.use(VueQuillEditor)

// 设置时间处理过滤器
Vue.filter("dateFormat", originval => {
    const date = new Date(originval)

    const year = date.getFullYear()
    //加一是因为其从0开始计数 加"是因为将它转为字符串
    const month = (date.getMonth() + 1 + "").padStart(1, "0")
    const day = (date.getDate() + "").padStart(1, "0")

    const hour = (date.getHours() + "").padStart(1, "0")
    const minute = (date.getMinutes() + "").padStart(1, "0")
    const second = (date.getSeconds() + "").padStart(1, "0")

    return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + second
})

new Vue({
    router,
    render: h => h(App)
}).$mount('#app')
